import { InjectionToken } from '@angular/core';
import { IManagedObject } from '@c8y/client';
import type * as L from 'leaflet';

export function getStatus(device: PositionManagedObject) {
  if (!device.c8y_ActiveAlarmsStatus) {
    return 'text-primary';
  }
  if (device.c8y_ActiveAlarmsStatus.critical) {
    return 'status critical';
  }
  if (device.c8y_ActiveAlarmsStatus.major) {
    return 'status major';
  }
  if (device.c8y_ActiveAlarmsStatus.warning) {
    return 'status warning';
  }
  if (device.c8y_ActiveAlarmsStatus.minor) {
    return 'status minor';
  }
  return 'text-primary';
}

export function getC8yMarker(marker: L.Marker, asset: PositionManagedObject) {
  (marker as C8yMarker).asset = asset;
  return marker as C8yMarker;
}

export const MAP_TILE_LAYER = new InjectionToken<MapTileLayer[]>('MAP_TILE_LAYER');

export type C8yMarker = L.Marker & { asset: PositionManagedObject };

export enum ClusterSize {
  NONE = 0,
  FOUR = 1,
  SIXTEEN = 2
}

export interface MapTileLayer {
  layerUrl: string;
  options: L.TileLayerOptions;
  label: string;
  priority?: number;
}

export interface PositionManagedObject extends IManagedObject {
  c8y_Position: {
    lat: number;
    lng: number;
  };

  c8y_ActiveAlarmsStatus?: {
    minor: number;
    major: number;
    warning: number;
    critical: number;
  };

  icon?: {
    name: string;
  };
}

export interface ClusterMapConfig extends MapConfig {
  center: [number, number];
  refreshInterval?: number;
}

export interface MapConfig {
  center?: [number, number];
  follow?: boolean;
  realtime?: boolean;
  zoomLevel?: number;
  icon?: string;
  color?: string;
  disableZoom?: boolean;
  disablePan?: boolean;
}

export const MAP_DEFAULT_CONFIG = new InjectionToken<MapDefaultConfig>('MAP_DEFAULT_CONFIG');

export type MapDefaultConfig = Pick<MapConfig, 'center' | 'zoomLevel'>;

