import { IterableChangeRecord, IterableDiffer, IterableDiffers } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import type * as L from 'leaflet';
import { gettext } from '@c8y/ngx-components';
import { C8yMarker, getStatus, PositionManagedObject } from './map.model';

export class ClusterMap {
  markers: C8yMarker[] = [];
  positions: PositionManagedObject[] = [];

  set clusterMarker(item: L.Layer) {
    this.removeClusterToBigMarker();
    this._clusterMarker = item;
  }

  get clusterMarker() {
    return this._clusterMarker;
  }

  set rect(item: L.Rectangle) {
    if (this._rect) {
      this._rect.remove();
    }
    this._rect = item;
  }

  get rect() {
    return this._rect;
  }

  private _clusterMarker: L.Layer;
  private _rect: L.Rectangle;
  private iterableDiffer: IterableDiffer<PositionManagedObject> | null;

  constructor(
    private iterable: IterableDiffers,
    private addAssetCallback: (asset: PositionManagedObject) => C8yMarker,
    private translateService: TranslateService
  ) {
    this.iterableDiffer = this.iterable.find(this.positions).create(this.trackBy);
  }

  render(map: L.Map) {
    if (this._rect) {
      this._rect.addTo(map);
    }
    this.updateChanges(map);
    if (this._clusterMarker) {
      this._clusterMarker.addTo(map);
    }
  }

  clear(map: L.Map) {
    this.removeClusterToBigMarker();
    this._rect.remove();
    this.positions = [];
    this.updateChanges(map);
  }

  removeClusterToBigMarker() {
    if (this._clusterMarker) {
      this._clusterMarker.remove();
      this._clusterMarker = null;
    }
  }

  addMarkerToMap(device: PositionManagedObject, map: L.Map) {
    const marker = this.addAssetCallback(device);
    this.markers.push(marker);
    marker.addTo(map);
  }

  setClusterToBigMarker(map: L.Map, count, leaflet: typeof L) {
    const bound = this.rect.getBounds();
    const text = this.translateService.instant(gettext('Zoom in'));
    const divMarker = leaflet.divIcon({
      html: `<div class="c8y-map-marker-count" data-count="${count}" title="${text}"></div>`
    });
    const labelIcon = leaflet.marker(bound.getCenter(), {
      icon: divMarker
    });
    labelIcon.addTo(map);
    labelIcon.on('click', () => {
      map.fitBounds(bound);
    });
    this.clusterMarker = labelIcon;
  }

  private updateChanges(map: L.Map) {
    const changes = this.iterableDiffer.diff(this.positions);
    if (changes) {
      changes.forEachRemovedItem((record: IterableChangeRecord<PositionManagedObject>) => {
        this.removeMarkerFromMap(record.item);
      });

      changes.forEachAddedItem((record: IterableChangeRecord<PositionManagedObject>) => {
        this.addMarkerToMap(record.item, map);
      });
    }
  }

  private trackBy(index: number, item: PositionManagedObject) {
    const trackItems = [item.id, item.c8y_Position.lat, item.c8y_Position.lng, getStatus(item)];
    return trackItems.join('');
  }

  private removeMarkerFromMap(device: PositionManagedObject) {
    const markers = this.markers.filter((marker: C8yMarker) => marker.asset?.id === device.id);
    markers.forEach(marker => marker.remove());
  }
}
