import { Component, EventEmitter, Input, Output, SimpleChanges } from '@angular/core';
import { ClusterMapComponent } from './cluster-map.component';
import { ClusterMapConfig } from './map.model';
import { map, takeUntil } from 'rxjs/operators';
import { Observable, Subject } from 'rxjs';
import { cloneDeep } from 'lodash-es';
@Component({
  selector: 'c8y-map-status',
  templateUrl: './map-status.component.html'
})
export class MapStatusComponent {
  @Input()
  config: ClusterMapConfig;

  @Output()
  configChange = new EventEmitter<ClusterMapConfig>();

  @Output()
  onUnfollow = new EventEmitter<ClusterMapConfig>();

  @Input()
  clusterMap: ClusterMapComponent;

  showCenter = false;
  secondsUntilRefresh$: Observable<string>;
  initConfig: ClusterMapConfig;
  private destroy$ = new Subject();

  ngOnInit(): void {
    this.initConfig = cloneDeep(this.config);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.clusterMap?.previousValue !== changes.clusterMap?.currentValue) {
      this.checkIfMapIsAlreadyCentered();

      this.secondsUntilRefresh$ = this.clusterMap.msUntilRefresh$.pipe(
        map(milliseconds => `${Math.floor(milliseconds / 1000)}`),
        takeUntil(this.destroy$)
      );
    }
  }

  center() {
    this.clusterMap.center();
  }

  reload() {
    this.clusterMap.reload();
  }

  cancelReload() {
    this.clusterMap.cancelReload();
  }

  toggleRealtime() {
    this.config = {
      ...this.config,
      realtime: !this.config.realtime
    };
    this.configChange.emit(this.config);
  }

  unfollow() {
    this.config = {
      ...this.clusterMap.config,
      follow: false
    };
    this.configChange.emit(this.config);
    this.onUnfollow.emit(this.config);
  }

  follow() {
    this.config = {
      ...this.config,
      follow: true
    };
    this.configChange.emit(this.config);
  }

  ngOnDestroy(): void {
    this.destroy$.next();
  }

  private checkIfMapIsAlreadyCentered() {
    this.clusterMap.mapChange.pipe(takeUntil(this.destroy$)).subscribe((event: L.LeafletEvent) => {
      if (this.config?.center && event.sourceTarget?.getBounds) {
        const bounds = event.sourceTarget.getBounds();
        this.showCenter = !bounds.getCenter().equals(this.config.center, 3);
      }
    });
  }
}
