import { Injectable } from '@angular/core';
import { IManagedObject, InventoryService, IResultList, Paging } from '@c8y/client';
import type * as L from 'leaflet';
import { ClusterSize, PositionManagedObject } from './map.model';

@Injectable({
  providedIn: 'root'
})
export class MapService {
  /**
   * The devices that are maximal displayed in one cluster.
   */
  MAX_DEVICE_PER_CLUSTER = 200;
  /**
   * The count until the cluster is sized. There are a maximum of
   * three clusters: 1, 4 or 16.
   */
  CLUSTER_LEVEL_THRESHOLD = 500;
  constructor(private inventory: InventoryService) {}

  /**
   * Returns the leaflet instance used by the cumulocity core.
   */
  async getLeaflet() {
    const c8yLeafletInstance = (await import('leaflet')).default;
    return c8yLeafletInstance;
  }

  /**
   * Verifies if a given managed object is a device with a position fragment.
   * @param mo The given managed object.
   */
  isPositionedDevice(mo: IManagedObject) {
    return !!(mo?.c8y_IsDevice && this.hasPosition(mo));
  }

  /**
   * Verifies if a given managed object has a position fragment.
   * @param mo The given managed object.
   */
  hasPosition(mo: IManagedObject) {
    return mo?.c8y_Position;
  }

  async getPositionMOs(
    bound: L.LatLngBounds,
    byGroupIdMO?: IManagedObject
  ): Promise<PositionManagedObject[]> {
    return this.getPositionMOsFromBound(bound, byGroupIdMO, false) as Promise<
      PositionManagedObject[]
    >;
  }

  async getPositionMOsFromBoundCount(
    bound: L.LatLngBounds,
    byGroupIdMO?: IManagedObject
  ): Promise<number> {
    return this.getPositionMOsFromBound(bound, byGroupIdMO, true) as Promise<number>;
  }

  async getPositionDevices(): Promise<PositionManagedObject[]>;
  async getPositionDevices(pageSize: number): Promise<PositionManagedObject[]>;
  async getPositionDevices(pageSize: number, count: false): Promise<PositionManagedObject[]>;
  async getPositionDevices(pageSize: number, count: true): Promise<number>;
  async getPositionDevices(
    pageSize = this.MAX_DEVICE_PER_CLUSTER,
    count?: boolean
  ): Promise<number | PositionManagedObject[]> {
    const { paging, data } = await this.inventory.list({
      pageSize: count ? 1 : pageSize,
      withTotalPages: count,
      query: '$filter=has(c8y_Position) and has(c8y_IsDevice)'
    });
    if (count) {
      return paging.totalPages;
    }
    return data as PositionManagedObject[];
  }

  async getPositionMOsFromBound(bound: L.LatLngBounds): Promise<PositionManagedObject[]>;
  async getPositionMOsFromBound(
    bound: L.LatLngBounds,
    byGroupIdMO?: IManagedObject
  ): Promise<PositionManagedObject[]>;
  async getPositionMOsFromBound(
    bound: L.LatLngBounds,
    byGroupIdMO: IManagedObject,
    count: false
  ): Promise<PositionManagedObject[]>;
  async getPositionMOsFromBound(
    bound: L.LatLngBounds,
    byGroupIdMO: IManagedObject,
    count: true
  ): Promise<number>;
  async getPositionMOsFromBound(
    bound: L.LatLngBounds,
    byGroupIdMO?: IManagedObject,
    count = false
  ): Promise<PositionManagedObject[] | number> {
    const { lat: latMin, lng: lngMin } = bound.getSouthWest();
    const { lat: latMax, lng: lngMax } = bound.getNorthEast();
    const byGroupIdFilter = byGroupIdMO
      ? `(bygroupid(${byGroupIdMO.id}) or id eq '${byGroupIdMO.id}') and `
      : '';
    const boundFilter = `$filter=${byGroupIdFilter}has(c8y_Position) and c8y_Position.lng gt ${lngMin}d and c8y_Position.lat gt ${latMin}d and c8y_Position.lng lt ${lngMax}d and c8y_Position.lat lt ${latMax}d`;
    const { paging, data } = await this.inventory.list({
      pageSize: count ? 1 : this.MAX_DEVICE_PER_CLUSTER,
      withTotalPages: count,
      query: boundFilter
    });
    if (count) {
      return paging.totalPages;
    }
    return data as PositionManagedObject[];
  }

  async getAllPositionsMOs(
    byGroupIdMO?: IManagedObject,
    pageSize = 500
  ): Promise<IResultList<PositionManagedObject>> {
    const filter: { pageSize: number; withTotalPages: boolean; query: string } = {
      pageSize,
      withTotalPages: true,
      query: 'has(c8y_Position)'
    };

    if (byGroupIdMO) {
      filter.query = `$filter=(bygroupid(${byGroupIdMO.id}) or id eq '${byGroupIdMO.id}') and has(c8y_Position)`;
    }

    const { paging, data, res } = await this.inventory.list(filter);

    return {
      res,
      paging: paging as Paging<PositionManagedObject>,
      data: data as PositionManagedObject[]
    };
  }

  async getClusterSize(bound: L.LatLngBounds) {
    const count = await this.getPositionMOsFromBoundCount(bound);
    let clusterSize = ClusterSize.NONE;
    if (count > this.CLUSTER_LEVEL_THRESHOLD) {
      clusterSize = ClusterSize.SIXTEEN;
    } else if (count > this.MAX_DEVICE_PER_CLUSTER) {
      clusterSize = ClusterSize.FOUR;
    }
    return clusterSize;
  }
}
