import {
  Component,
  EventEmitter,
  Inject,
  Input,
  IterableDiffers,
  Optional,
  Output,
  SimpleChange,
  SimpleChanges
} from '@angular/core';
import { IManagedObject } from '@c8y/client';
import { ColorService, ManagedObjectRealtimeService } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import type * as L from 'leaflet';
import { BehaviorSubject, from, fromEvent, interval, merge, NEVER, of, Subject } from 'rxjs';
import {
  debounceTime,
  filter,
  first,
  map,
  mergeMap,
  switchMap,
  takeUntil,
  tap
} from 'rxjs/operators';
import { ClusterMap } from './cluster-map';
import { MapComponent } from './map.component';
import {
  ClusterMapConfig,
  ClusterSize,
  defaultLayer,
  defaultMapConfig,
  MapDefaultConfig,
  MapTileLayer,
  MAP_DEFAULT_CONFIG,
  MAP_TILE_LAYER,
  PositionManagedObject
} from './map.model';
import { MapService } from './map.service';

@Component({
  selector: 'c8y-cluster-map',
  templateUrl: './cluster-map.component.html',
  providers: [ManagedObjectRealtimeService]
})
export class ClusterMapComponent extends MapComponent {
  isLoading$ = new BehaviorSubject(false);
  msUntilRefresh$ = new BehaviorSubject(5000);

  @Input()
  config: ClusterMapConfig;

  @Input()
  rootNode: IManagedObject;

  @Input('asset')
  assets: PositionManagedObject;

  @Input()
  showClusterColor = false;

  @Output()
  mapChange = new EventEmitter<L.LeafletEvent>();

  private reloadTrigger$ = new BehaviorSubject(false);
  private clusters: ClusterMap[] = [];
  private readonly MIN_INTERVAL = 5000;
  private readonly EVENT_THROTTLE_TIME = 750;

  constructor(
    protected moRealtimeService: ManagedObjectRealtimeService,
    protected mapService: MapService,
    @Optional() @Inject(MAP_TILE_LAYER) protected layers: Array<MapTileLayer | MapTileLayer[]>,
    @Optional() @Inject(MAP_DEFAULT_CONFIG) protected defaultConfig: MapDefaultConfig,
    protected translateService: TranslateService,
    private iterable: IterableDiffers,
    private colorService: ColorService
  ) {
    super(moRealtimeService, mapService, layers, defaultConfig, translateService);
    if (!this.layers) {
      this.layers = [defaultLayer];
    }
    if (!this.defaultConfig) {
      this.defaultConfig = defaultMapConfig;
    }
    this.config = {
      center: this.defaultConfig.center
    };
  }

  async ngOnChanges(changes: SimpleChanges) {
    if (changes.config?.firstChange) {
      return;
    }

    if (changes.rootNode?.previousValue !== changes.rootNode?.currentValue) {
      this.changeRootNode(changes.rootNode.currentValue);
    }

    if (changes.config?.currentValue) {
      this.changeConfig(changes.config);
    }
  }

  changeConfig(change: SimpleChange) {
    // on following, cancel reload to avoid stale state
    if (change.currentValue.follow === true) {
      this.cancelReload();
      this.isLoading$.next(false);
    }

    if (change.currentValue.refreshInterval !== change.previousValue.refreshInterval) {
      this.reload();
    }
    super.changeConfig(change);
  }

  async ngAfterViewInit() {
    if (!this.leaflet) {
      this.leaflet = await this.mapService.getLeaflet();
    }
    this.initMap();
    this.changeRootNode(this.rootNode);
    this.changeConfig(new SimpleChange({}, this.config, false));
  }

  async reset() {
    this.ngOnDestroy();
    await this.ngAfterViewInit();
  }

  reload() {
    this.reloadTrigger$.next(true);
  }

  cancelReload() {
    this.reloadTrigger$.next(false);
  }

  listenToClusterChanges() {
    const timerStart$ = new Subject();
    const timerEnd$ = new Subject();

    const documentHiddenEvent$ = fromEvent<boolean>(document, 'visibilitychange').pipe(
      takeUntil(this.destroy$)
    );

    const interval$ = timerStart$.pipe(
      map(() => this.config.refreshInterval),
      switchMap(configInterval => {
        if (!configInterval) {
          return NEVER;
        }
        return interval(1000).pipe(
          map(value => value * 1000),
          tap(value => this.msUntilRefresh$.next(configInterval - value)),
          filter(value => value >= this.MIN_INTERVAL && value >= configInterval),
          first(),
          takeUntil(timerEnd$)
        );
      }),
      switchMap(() => (document.hidden ? documentHiddenEvent$ : of(true))),
      takeUntil(this.destroy$)
    );

    const mapChange$ = merge(
      fromEvent<L.LeafletEvent>(this.map, 'move'),
      fromEvent<L.LeafletEvent>(this.map, 'moveend')
    ).pipe(
      debounceTime(this.EVENT_THROTTLE_TIME),
      tap(event => this.mapChange.emit(event)),
      takeUntil(this.destroy$)
    );

    merge(this.reloadTrigger$, mapChange$, interval$)
      .pipe(
        tap(() => {
          timerEnd$.next(true);
          this.msUntilRefresh$.next(0);
          this.isLoading$.next(true);
        }),
        switchMap(value =>
          value === false
            ? of([])
            : from(this.mapService.getClusterSize(this.map.getBounds())).pipe(
                mergeMap((clusterSize: ClusterSize) =>
                  this.getClusterRects(clusterSize, this.map.getBounds())
                ),
                mergeMap(rects => this.createOrUpdateCluster(rects))
              )
        ),
        takeUntil(this.destroy$)
      )
      .subscribe((clusters: ClusterMap[]) => {
        clusters.forEach(cluster => cluster.render(this.map));

        this.isLoading$.next(false);
        timerStart$.next();
        this.msUntilRefresh$.next(this.config.refreshInterval);
      });
  }

  refreshMarkers() {
    if (this.assets) {
      super.refreshMarkers();
      return;
    }
    this.clusters.forEach(cluster => {
      cluster.clear(this.map);
    });
    this.reload();
  }

  private changeRootNode(mo: IManagedObject) {
    this.unsubscribeAllListeners();
    this.clearMarkers();
    this.clearClusters();

    const isPositionDevice = mo?.c8y_Position && mo?.c8y_IsDevice;
    if (isPositionDevice) {
      this.assets = mo as PositionManagedObject;
      this.refreshMarkers();
      return;
    }
    this.assets = null;
    this.listenToClusterChanges();
    this.reload();
  }

  private async getClusterRects(
    levelThreshold: ClusterSize = ClusterSize.FOUR,
    viewBounds: L.LatLngBounds,
    level = 0
  ): Promise<L.Rectangle[]> {
    let rects = [];

    if (levelThreshold === ClusterSize.NONE) {
      const rect = await this.getRect(viewBounds);
      rects.push(rect);
      return rects;
    }

    if (level >= levelThreshold) {
      return rects;
    }
    level++;

    const { lat: x1, lng: y1 } = viewBounds.getSouthWest();
    const { lat: x2, lng: y2 } = viewBounds.getNorthEast();
    const newX2 = (x1 + x2) / 2;
    const newY2 = (y1 + y2) / 2;

    const bounds: [[number, number], [number, number]][] = [
      [
        [x1, y1],
        [newX2, newY2]
      ],
      [
        [newX2, newY2],
        [x2, y2]
      ],
      [
        [x1, newY2],
        [newX2, y2]
      ],
      [
        [newX2, y1],
        [x2, newY2]
      ]
    ];
    for (const bound of bounds) {
      const latLngBound = this.leaflet.latLngBounds(bound);
      const rect = await this.getRect(latLngBound);
      rects = [...rects, ...(await this.getClusterRects(levelThreshold, latLngBound, level))];

      if (level === levelThreshold) {
        rects.push(rect);
      }
    }

    return rects;
  }

  private async getRect(latLngBound: L.LatLngBounds) {
    let color = 'none';
    if (this.showClusterColor) {
      color = await this.colorService.generateColor(latLngBound.toBBoxString());
    }
    const rect = this.leaflet.rectangle(latLngBound, {
      color,
      weight: color === 'none' ? 0 : 1,
      interactive: false
    });
    return rect;
  }

  private clearClusters() {
    this.clusters.forEach(cluster => {
      cluster.clear(this.map);
    });
    this.clusters = [];
  }

  private async updateCluster(cluster: ClusterMap) {
    const clusterCount = await this.mapService.getPositionMOsFromBoundCount(
      cluster.rect.getBounds(),
      this.rootNode
    );
    if (clusterCount > this.mapService.MAX_DEVICE_PER_CLUSTER) {
      cluster.setClusterToBigMarker(this.map, clusterCount, this.leaflet);
      cluster.positions = [];
      return cluster;
    }

    cluster.removeClusterToBigMarker();
    cluster.positions = await this.mapService.getPositionMOs(
      cluster.rect.getBounds(),
      this.rootNode
    );
    return cluster;
  }

  private createOrUpdateCluster(rects: L.Rectangle<unknown>[]) {
    const isNew = rects.length !== this.clusters.length;
    if (isNew) {
      this.clearClusters();
    }
    const updatePromise = rects.map((rect, index) => {
      if (isNew) {
        const cluster = new ClusterMap(
          this.iterable,
          asset => this.getAssetMarker(asset),
          this.translateService
        );
        this.clusters.push(cluster);
      }
      this.clusters[index].rect = rect;
      return this.updateCluster(this.clusters[index]);
    });

    return Promise.all(updatePromise);
  }
}
